.rate {
  display: inline-block;
  list-style: none;
  padding: 0;
  margin: 0;
  vertical-align: middle; }
  .rate-star {
    display: inline-block;
    position: relative;
    transition: all 0.3s; }
    .rate-star:hover {
      transform: scale(1.1); }
    .rate-star:not(:last-child) {
      margin-right: 8px; }
    .rate-star-pre {
      display: inline-block;
      position: absolute;
      top: 0;
      left: 0;
      width: 50%;
      height: 100%;
      overflow: hidden;
      opacity: 0; }
    .rate-star-pre, .rate-star-after {
      transition: all 0.3s;
      color: #d9d9d9;
      font-size: 20px;
      cursor: pointer;
      user-select: none; }
      .rate-star-pre .icon, .rate-star-after .icon {
        font-size: 20px; }
    .rate-star.rate-star-half .rate-star-pre {
      opacity: 1;
      color: #ff9500; }
    .rate-star.rate-star-full .rate-star-after {
      color: #ff9500; }
    .rate-star.rate-star-zero .rate-star-full,
    .rate-star.rate-star-zero .rate-star-after {
      color: #d9d9d9; }



