.alert {
  position: relative;
  color: #595959;
  border-width: 1px;
  border-style: solid;
  padding: 8px;
  border-radius: 4px; }
  .alert.alert-size-default {
    font-size: 14px; }
    .alert.alert-size-default.alert-with-icon {
      padding-left: 34px; }
    .alert.alert-size-default .alert-icon {
      top: 11.5px; }
  .alert.alert-size-large {
    font-size: 16px; }
    .alert.alert-size-large.alert-with-icon {
      padding-left: 34px; }
    .alert.alert-size-large .alert-icon {
      top: 12px; }
  .alert.alert-size-small {
    font-size: 14px; }
    .alert.alert-size-small.alert-with-icon {
      padding-left: 34px; }
    .alert.alert-size-small .alert-icon {
      top: 11.5px; }
  .alert.alert-size-tiny {
    font-size: 12px; }
    .alert.alert-size-tiny.alert-with-icon {
      padding-left: 34px; }
    .alert.alert-size-tiny .alert-icon {
      top: 11px; }
  .alert.alert-closable {
    padding-right: 34px; }
  .alert.alert-type-info {
    background-color: #f2f6fe;
    border-color: #2e6cf6; }
    .alert.alert-type-info .alert-icon {
      color: #0a4ee7; }
  .alert.alert-type-success {
    background-color: #e0f5e5;
    border-color: #44c566; }
    .alert.alert-type-success .alert-icon {
      color: #33a350; }
  .alert.alert-type-warning {
    background-color: #ffeacc;
    border-color: #ff9500; }
    .alert.alert-type-warning .alert-icon {
      color: #cc7700; }
  .alert.alert-type-danger {
    background-color: #fff4f2;
    border-color: #ff4a26; }
    .alert.alert-type-danger .alert-icon {
      color: #f22800; }
  .alert.alert-play {
    padding-top: 0;
    padding-bottom: 0; }
    .alert.alert-play .alert-content-item {
      padding-top: 8px;
      padding-bottom: 8px; }
  .alert-title {
    display: inline-block;
    font-size: 1.2em;
    padding-bottom: 8px; }
  .alert-content {
    line-height: 1.5;
    transition: all 0.3s; }
  .alert-icon {
    position: absolute;
    left: 12px; }
  .alert-close {
    position: absolute;
    top: 8px;
    right: 12px;
    cursor: pointer; }



