.page {
  padding: 0 24px;
  box-sizing: border-box; }
  .page-part {
    max-width: 840px;
    padding-right: 48px;
    width: 80%;
    box-sizing: border-box; }
    .page-part:not(:first-child) {
      padding-top: 40px; }
    .page-part:not(:last-child) {
      padding-bottom: 40px;
      border-bottom: 1px solid #e6e6e6; }
    .page-part-title {
      display: flex;
      align-items: center;
      font-size: 24px;
      font-weight: 400;
      color: #595959;
      margin: 0 0 24px 0; }
      .page-part-title:before {
        display: inline-block;
        content: "";
