.item {
  display: flex;
  padding: 12px 24px;
  overflow: hidden;
  border-bottom: 1px solid #d9d9d9; }
  .item:last-child {
    border-bottom: 0; }
  .item a {
    display: flex;
    text-decoration: none; }
  .item .title {
    color: #595959; }
  .item .desc {
    color: #8c8c8c; }
  .item.isTableStyle {
    padding: 0;
    min-height: 54px;
    vertical-align: middle;
    text-align: left; }
    .item.isTableStyle .cell-wrap {
      flex: 1;
      display: flex; }
      .item.isTableStyle .cell-wrap .cell-wrap-name {
        width: 100px;
        line-height: 54px;
        flex-shrink: 0;
        text-indent: 16px;
        font-weight: bold;
        color: #595959;
        background-color: #f2f2f2; }
      .item.isTableStyle .cell-wrap .cell-wrap-desc {
        padding: 16px 0;
        font-size: 14px;
        color: #595959; }
        .item.isTableStyle .cell-wrap .cell-wrap-desc.is-bold {
          font-weight: bold; }
        .item.isTableStyle .cell-wrap .cell-wrap-desc a {
          color: #595959; }
        .item.isTableStyle .cell-wrap .cell-wrap-desc.link a {
          margin: 0 16px;
          color: #2e6cf6; }
        .item.isTableStyle .cell-wrap .cell-wrap-desc .text {
          margin: 0 16px; }
    .item.isTableStyle:first-child .cell-wrap-name {
      border-top-left-radius: 4px; }
    .item.isTableStyle:last-child .cell-wrap-name {
      border-bottom-left-radius: 4px; }
  .item img {
    width: 32px;
    height: 32px;
    margin-top: 3px;
    margin-right: 16px;
    border-radius: 50%; }
  .item .content {
    width: auto; }



