.tag {
  display: inline-flex;
  flex-wrap: wrap;
  padding: 3px 5px;
  align-items: center;
  color: #595959;
  transition: all 0.3s;
  border-radius: 4px;
  border: 1px solid transparent;
  font-size: 12px;
  line-height: 1.5;
  cursor: pointer; }
  .tag:hover {
    color: #2e6cf6; }
  .tag:not(:last-child) {
    margin-right: 5px; }
  .tag.has-border {
    border-color: #d9d9d9; }
  .tag.tag-has-color {
    color: #fff; }

.tag-gray {
  color: #595959;
  background-color: #bfbfbf;
  background-color: #e6e6e6; }
  .tag-gray.has-border {
    border: 1px solid #a6a6a6; }
  .tag-gray:hover {
    color: #333333; }
  .tag-gray.has-border {
    border-color: #d9d9d9; }

.tag-primary {
  color: #2e6cf6;
  background-color: #f2f6fe; }
  .tag-primary.has-border {
    border: 1px solid #c1d3fc; }
  .tag-primary:hover {
    color: #0946cf; }

.tag-danger {
  color: #ff4a26;
  background-color: #fff4f2; }
  .tag-danger.has-border {
    border: 1px solid #ffcabf; }
  .tag-danger:hover {
    color: #d92400; }

.tag-warning {
  color: #ff9500;
  background-color: #ffeacc; }
  .tag-warning.has-border {
    border: 1px solid #ffd599; }
  .tag-warning:hover {
    color: #b36800; }

.tag-success {
  color: #44c566;
  background-color: #e0f5e5; }
  .tag-success.has-border {
    border: 1px solid #b9e9c6; }
  .tag-success:hover {
    color: #2d9047; }

.tag.is-checked {
  color: #fff;
  background-color: #2e6cf6;
  border-color: #2e6cf6; }
  .tag.is-checked:hover {
    color: #fff;
    background-color: #0a4ee7; }

.tag-close-btn {
  display: inline-block;
  padding-left: 5px;
  cursor: pointer; }



