.layout {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  flex: 1; }
  .layout.has-sider {
    flex-direction: row; }
    .layout.has-sider .layout-content {
      flex: 1; }
  .layout-header, .layout-footer, .layout-content {
    width: 100%;
    box-sizing: border-box; }
  .layout-sider {
    position: relative;
    transition: all 0.3s; }
    .layout-sider-children {
      height: 100%;
      padding-top: 0.1px;
      margin-top: -0.1px; }



