.breadcrumb {
  display: flex;
  box-sizing: border-box; }
  .breadcrumb-item {
    display: flex;
    font-size: 12px;
    color: #8c8c8c;
    line-height: 17px; }
    .breadcrumb-item-title:hover {
      color: #2e6cf6;
      cursor: pointer; }
  .breadcrumb .show {
    display: inline-block;
    margin: 0 6px; }
  .breadcrumb .hide {
    display: none; }
  .breadcrumb .current {
    color: #262626; }



