.tooltip {
  display: inline-block; }
  .tooltip-message {
    padding: 6px 8px;
    border-radius: 4px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.15);
    font-size: 14px; }
  .tooltip-message-light {
    background: #fff; }
  .tooltip-message-dark {
    background: #595959;
    color: #fff; }



