.popconfirm {
  padding: 12px 16px;
  color: #595959;
  box-sizing: border-box;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.15); }
  .popconfirm-header-content {
    box-sizing: border-box;
    font-weight: 500;
    padding-left: 8px; }
  .popconfirm-content {
    box-sizing: border-box;
    padding: 16px 0 16px 24px; }
  .popconfirm-footer {
    text-align: right; }
  .popconfirm-warning .icon {
    color: #ff9500; }
  .popconfirm-info .icon {
    color: #2e6cf6; }
  .popconfirm-success .icon {
    color: #44c566; }
  .popconfirm-error .icon {
    color: #ff4a26; }



