.message-wrapper {
  position: fixed;
  top: 100px;
  width: 100%;
  z-index: 4000; }

.message {
  text-align: center; }
  .message:not(:last-child) {
    margin-bottom: 10px; }
  .message-content {
    color: #595959;
    display: inline-block;
    border-radius: 4px;
    padding: 5px 10px;
    background-color: #fff;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.15); }

.message-info .icon {
  font-size: 14px;
  color: #2e6cf6; }

.message-warning .icon {
  font-size: 14px;
  color: #ff9500; }

.message-error .icon {
  font-size: 14px;
  color: #ff4a26; }

.message-success .icon {
  font-size: 14px;
  color: #44c566; }



