.list {
  position: relative;
  font-size: 14px;
  font-variant: tabular-nums;
  line-height: 1.5;
  color: #595959;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none; }
  .list header,
  .list footer {
    padding: 12px 24px; }
  .list header {
    border-bottom: 1px solid #d9d9d9; }
  .list-custom {
    display: flex;
    padding: 12px 24px;
    overflow: hidden;
    border-bottom: 1px solid #d9d9d9; }
  .list.bordered {
    border-radius: 4px;
    border: 1px solid #d9d9d9; }
  .list.default .item {
    padding: 12px 24px; }
  .list.small {
    font-size: 12px; }
    .list.small .item,
    .list.small header,
    .list.small footer {
      padding: 8px 24px; }
  .list.large {
    font-size: 16px; }
    .list.large .item,
    .list.large header,
    .list.large footer {
      padding: 16px 24px; }



