.modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0 auto;
  overflow: auto;
  z-index: 3000;
  box-sizing: border-box; }
  .modal.is-show {
    display: block; }
  .modal-mask {
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.45); }
  .modal-wrapper {
    position: relative;
    z-index: 2;
    margin: 0 auto;
    top: 100px; }
  .modal-size-large {
    width: 75%; }
  .modal-size-default {
    width: 50%; }
  .modal-size-small {
    width: 420px; }
  .modal-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background-color: #fff;
    border: none;
    border-radius: 4px;
    box-sizing: border-box;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.15); }
    .modal-content.modal-confirm {
      padding: 8px; }
      .modal-content.modal-confirm .modal-close {
        right: 24px; }
    .modal-content-header {
      position: relative;
      padding: 16px 26px 16px 16px;
      box-sizing: border-box; }
      .modal-content-header-title {
        display: flex; }
        .modal-content-header-title .title-center {
          display: flex;
          justify-content: center;
          flex: 1; }
    .modal-content-body {
      flex: 1;
      padding: 8px 16px;
      font-size: 14px;
      color: #595959;
      overflow-y: auto;
      box-sizing: border-box; }
    .modal-content-footer {
      display: inline-block;
      width: 100%;
      text-align: right;
      padding: 16px;
      box-sizing: border-box; }
  .modal-close {
    position: absolute;
    top: 15px;
    right: 16px;
    border: 0;
    background-color: transparent;
    cursor: pointer;
    outline: none;
