  .modal-icon-success {
    color: #44c566; }
  .modal-icon-warning {
    color: #ff9500; }
  .modal-icon-info {
    color: #2e6cf6; }
  .modal-icon-error {
    color: #ff4a26; }

.mode-scale-appear,
.mode-scale-enter {
  transform: scale(0);
  transition: all 0.2s; }

.mode-scale-appear-active,
.mode-scale-enter-active {
  transform: scale(1); }

.mode-scale-enter-done {
  transform: scale(1); }

.mode-scale-exit {
  transform: scale(1);
  transition: all 0.2s; }

.mode-scale-exit-active,
.mode-scale-exit-done {
  transform: scale(0); }

.mode-scale-exit-done {
  display: none; }

.mode-fade-appear,
.mode-fade-enter {
  opacity: 0;
  transition: all 0.2s; }

.mode-fade-appear-active,
.mode-fade-enter-active {
  opacity: 1; }

.mode-fade-exit {
  opacity: 1;
  transition: all 0.2s; }

.mode-fade-exit-active,
.mode-fade-exit-done {
  opacity: 0; }

.mode-fade-enter-done {
  opacity: 1; }

.mode-fade-exit-done {
  display: none; }



