.switch {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  width: 44px;
  height: 22px;
  line-height: 20px;
  padding: 0;
  vertical-align: middle;
  border-radius: 20px;
  border: 1px solid #d9d9d9;
  background-color: #d9d9d9;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  outline: none; }
  .switch-inner {
    color: #fff;
    font-size: 12px;
    position: absolute;
    left: 24px;
    top: 0; }
  .switch:after {
    position: absolute;
    width: 18px;
    height: 18px;
    left: 2px;
    top: 1px;
    border-radius: 50% 50%;
    background-color: #fff;
    content: " ";
    cursor: pointer;
    transition: all 0.3s ease-in-out; }
  .switch.switch-enable:hover:after {
    transform: scale(1.1); }
  .switch-checked {
    border: 1px solid #2e6cf6;
    background-color: #2e6cf6; }
    .switch-checked .switch-inner {
      left: 6px; }
    .switch-checked:after {
      left: 22px; }
  .switch-disabled {
    cursor: not-allowed;
    opacity: 0.4; }
    .switch-disabled::after {
      cursor: not-allowed; }
  .switch-label {
    display: inline-block;
    line-height: 20px;
    font-size: 14px;
    padding-left: 10px;
    vertical-align: middle;
    white-space: normal;
    pointer-events: none;
    user-select: text; }
  .switch-small {
    width: 28px;
    height: 16px;
    line-height: 14px;
    border-radius: 14px; }
    .switch-small:after {
      width: 12px;
      height: 12px; }
    .switch-small .switch-inner {
      left: 3px;
      transform: scale(0.8); }
    .switch-small.switch-checked:after {
      left: 14px; }
  .switch-large {
    width: 56px;
    height: 28px;
    line-height: 26px;
    border-radius: 26px; }
    .switch-large:after {
      width: 24px;
      height: 24px; }
    .switch-large .switch-inner {
      left: 8px;
      font-size: 14px; }
    .switch-large.switch-checked:after {
      left: 30px; }



