.progress {
  position: relative;
  font-size: 14px; }
  .progress-outer {
    display: inline-block;
    width: 100%;
    box-sizing: border-box; }
    .progress-outer:not(:last-child) {
      padding-right: calc(2em + 8px);
      margin-right: calc(-2em - 8px); }
  .progress-bar {
    display: inline-block;
    position: relative;
    width: 100%;
    background-color: #f2f2f2;
    border-radius: 100px;
    vertical-align: middle;
    box-sizing: border-box; }
    .progress-bar-bg {
      width: 30%;
      height: 8px;
      border-radius: 100px;
      transition: width 0.2s ease-in-out;
      background: #2e6cf6; }
    .progress-bar-success-bg {
      position: absolute;
      top: 0;
      left: 0; }
    .progress-bar-normal {
      background: #2e6cf6; }
    .progress-bar-warning {
      background: #ff9500; }
    .progress-bar-exception {
      background: #ff4a26; }
    .progress-bar-success {
      background: #44c566; }
    .progress-bar-active:before {
      content: "";
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: #fff;
      border-radius: 10px;
      animation: active 2.4s ease-out infinite; }
  .progress-text {
    display: inline-block;
    width: 2em;
    text-align: left;
    vertical-align: middle;
    white-space: nowrap;
    color: #8c8c8c;
    line-height: 1;
    margin-left: 8px; }
  .progress-circle {
    display: inline-block;
    position: relative; }
    .progress-circle .progress-text {
      position: absolute;
      width: 100%;
      text-align: center;
      top: 50%;
      left: 0;
      margin: 0;
      transform: translateY(-50%); }
    .progress-circle-path {
      transition: all 0.2s ease-in-out; }

@keyframes active {
  0% {
    opacity: 0.1;
    width: 0; }
  20% {
    opacity: 0.5;
    width: 0; }
  100% {
    opacity: 0;
    width: 100%; } }



