.badge {
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  line-height: 1;
  margin: 0;
  padding: 0; }
  .badge-count {
    display: block;
    color: #fff;
    text-align: center;
    font-weight: 400;
    font-size: 12px;
    padding: 0 4px;
    min-width: 14px;
    height: 14px;
    line-height: 14px;
    border-radius: 10px; }
  .badge-dot {
    display: block;
    position: relative;
    width: 10px;
    height: 10px;
    border-radius: 50%; }
  .badge-absolute {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    transform: translate(60%, -60%); }
  .badge-error {
    background: #ff4a26; }
  .badge-warning {
    background: #ff9500; }
  .badge-success {
    background: #44c566; }
  .badge-default {
    background: #2e6cf6; }
  .badge-processing:after {
    content: "";
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    background: inherit;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    animation: progress 1.2s infinite ease-in-out; }

@keyframes progress {
  0% {
    transform: scale(1);
    transform-origin: center;
    opacity: .5; }
  100% {
    transform: scale(1.7);
    transform-origin: center;
    opacity: 0; } }



