.popover {
  display: inline-block;
  position: relative;
  outline: none; }
  .popover-container {
    display: block;
    position: absolute;
    z-index: 1000;
    outline: none; }
  .popover-content {
    background-color: #fff;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.15);
    padding: 6px;
    border-radius: 4px; }
  .popover-children-container {
    display: inline-block;
    position: relative;
    width: 100%;
    vertical-align: baseline; }

.dropdown-enter,
.dropdown-appear {
  transform: scaleY(0);
  transform-origin: top center; }

.dropdown-enter-active,
.dropdown-appear-active,
.dropdown-enter-done {
  transition: all 0.2s ease-in-out;
  transform: scaleY(1); }

.dropdown-exit {
  transform-origin: top center;
  transform: scaleY(1); }

.dropdown-exit-active {
  transition: all 0.2s ease-in-out;
  transform: scaleY(0); }

.dropdown-exit-done {
  display: none; }

.is-hide {
  display: none; }



